import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber, Button, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime, setTimesAdditionalRegistration } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Học kỳ:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm học:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Kỳ đăng ký">
              {getFieldDecorator('setTimesAdditionalRegistration', {})(
                <Select>
                  {setTimesAdditionalRegistration.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Mã SV">
              <Input placeholder="Nhập mã SV" />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label=" ">
              <Button type="primary">Tìm kiếm</Button>
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={6}>
            Số TC tối thiểu: <b>16</b>
          </Col>
          <Col span={6}>
            Số TC tối đa: <b>24</b>
          </Col>
          <Col span={6}>
            Họ tên: <b>Hoàng Thị Nhung</b>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



